import React, { Component } from 'react'
import {userConsumer} from './userContext'

class ClassC extends Component {
    render() {
        return (
            
                <userConsumer>
                    {userContext => {
                        return  <h2> {userContext} </h2>
                    }}
                </userConsumer >
            
        )
    }
}

export default ClassC